.window {
    border: 0;
    box-shadow: 2px 2px 6px black;
    margin-bottom: 0.5em;

    .window-header {
        background: -moz-linear-gradient(top, #686661 0%, #3d3c38 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top, #686661 0%,#3d3c38 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom, #686661 0%,#3d3c38 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        height: 32px; 
        color: white;
        font-weight: bold;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;

        .title {
            margin-left: 7px;
            width: 100%;
        }
        .btn-close, .btn-minimize, .btn-maximize {
            width: 19px;
            height: 19px;
            padding: 1px 1px 1px 1px;
        }
        .btn-close:hover, .btn-minimize:hover, .btn-maximize:hover {
            cursor: pointer;
        }
        .btn-close:hover {
            background: url(/img/close_focused_prelight.svg) no-repeat;
        }
        .btn-minimize:hover {
            background: url(/img/minimize_focused_prelight.svg) no-repeat;
        }
        .btn-maximize:hover {
            background: url(/img/maximize_focused_prelight.svg) no-repeat;
        }
        .btn-close {
            background: url(/img/close.svg) no-repeat;
        }
        .btn-minimize {
            background: url(/img/minimize.svg) no-repeat;
        }
        .btn-maximize {
            background: url(/img/maximize.svg) no-repeat;
        }
    }

    .window-body {
        padding-top: 1em;
    }
}
