body {
    background-color: #cccccc;
    font-size: 16px;
}

.MJXc-display {
    text-align: left !important;
    margin-left: 0.5em !important;
}

.mjx-block {
    text-align: left !important;
}

.fluid {
    margin: 0;
    padding: 1px;
}

.hide {
    display: none;
}

.selectable-code:focus {
    outline: none;
}

.btn-load-cmt {
    margin-bottom: 1em;
    box-shadow: 2px 2px 6px green;
    font-family: 'Ubuntu Mono', monospace;
}

.form-control {
    border-radius: 0;
}

pre code {
    font-family: 'Ubuntu Mono', monospace;
    font-size: 16px;

    overflow: scroll;
    word-wrap: normal;
    overflow-wrap: normal;
    white-space: pre;
}

pre {
    border-radius: 0;
    border: 1px solid #ccc;
    padding: 0.8em;
}

img {
    max-width: 100%;
    max-height: 100%;
}
