$footer-height: 3em;

.page-footer {
    background-color: #300a24;
    color: white;
    height: $footer-height;
    padding: 1em;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0; 
}

html {
    height: 100%;
}

body {
    position: relative;
    min-height: 100%;
    padding-bottom: $footer-height;
}
