.brand {
    padding:10px;
    font-family: 'Ubuntu Mono', monospace;
    background-color: #370d28;
    color: #fff;
    margin: 0;
    margin-bottom: 8px;
    box-shadow: 0px 3px 3px black;

    .keyword {
        color: #8ae230;
    }
    .string, .string a {
        color: lighten(#cc0219, 5%);
    }
    .string a:hover {
        color: lighten(#cc0219, 10%);
    }
    .op {
        color: #af5f00;
    }
}
